/* ======= QUOTES ======= */

.cl-quote {
    
    display: block;
    margin: 0 0 2rem;
    padding: 2rem 0;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    text-align: center;
    
    .cl-quote-image {
        display: block;
        width: 100px;
        height: 100px;
        margin: 0 auto 1rem;
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
        border-radius: 50%;
    }
    
    blockquote,
    cite {
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: calc( 100% - 4rem );
        max-width: 800px;
    }
    
    blockquote {
        font-style: normal;
        line-height: 1.5rem;
        color: inherit;
    }
    
    cite {
        font-size: 80%;
        color: $darkgrey;
        position: relative;
        left: -.5rem;
		margin-top: 1rem;
		margin-bottom: .5rem;
    }
    
    cite:before {
        content: '';
        display: inline-block;
        width: 1rem;
        height: 1px;
        background: $darkgrey;
        position: relative;
        bottom: .25rem;
        margin-right: .25rem;
    }
    
}

@media screen and (min-width: $break1) {
	.cl-quote {
		blockquote,
		cite {
			width: 60%;
			min-width: 500px;
		}
	}
}